import {
  Typography,
  Box,
  Paper,
  Grid,
  Button,
  TextField,
  IconButton,
  FormHelperText,
} from "@material-ui/core";
import React, { useState, useContext, useEffect } from "react";
import SendIcon from "@material-ui/icons/Send";
import { makeStyles } from "@material-ui/core/styles";
import { BiCommentDots } from "react-icons/bi";
import { RiShareForwardLine } from "react-icons/ri";
import { withStyles } from "@material-ui/core/styles";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemText from "@material-ui/core/ListItemText";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Dialog from "@material-ui/core/Dialog";

import DialogContent from "@material-ui/core/DialogContent";

import DialogTitle from "@material-ui/core/DialogTitle";
// import SendIcon from "@material-ui/icons/Send";
import { Link, useHistory } from "react-router-dom";
import { FaEllipsisH } from "react-icons/fa";
import { MdCancel } from "react-icons/md";
import Axios from "axios";
import Apiconfigs from "src/ApiConfig/ApiConfig";
import ButtonCircularProgress from "./ButtonCircularProgress";
import { toast } from "react-toastify";
import LoadingSkeleton from "./LoadingSkeleton";
import { AuthContext } from "src/context/Auth";
import { websiteName } from "src/ApiConfig/ApiConfig";
import moment from "moment";
import { tokenName } from "src/utils";
import { BsFillArrowUpRightSquareFill } from "react-icons/bs";
import SocialShareBox from "./SocialShareBox";
import CommentPromotionBox from "./CommentPromotionBox";
import Picker from "emoji-picker-react";
import { BsEmojiLaughing } from "react-icons/bs";

const Accordion = withStyles({
  root: {
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "0",
    },
  },
  expanded: {},
})(MuiAccordion);
const AccordionSummary = withStyles({
  root: {
    marginBottom: -1,
    minHeight: "auto",
    "&$expanded": {
      minHeight: "auto",
    },
  },
  content: {
    margin: "0px 0 ",
    justifyContent: "center",
    "&$expanded": {
      margin: "0px 0 ",
    },
  },
  expanded: {},
})(MuiAccordionSummary);
const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    [theme.breakpoints.down("xs")]: {
      padding: "10px 0 !important",
    },
  },
}))(MuiAccordionDetails);
const StyledMenuItem = withStyles((theme) => ({
  root: {
    "&:focus": {
      backgroundColor: theme.palette.primary.main,
      "& .MuiListItemIcon-root, & .MuiListItemText-primary": {
        color: theme.palette.common.white,
      },
    },
  },
}))(MenuItem);
const StyledMenu = withStyles({
  paper: {
    border: "1px solid #484849 !important",
    backgroundColor: "#101010 !important",
    minWidth: "120px !important",
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "center",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "center",
    }}
    {...props}
  />
));
const useStyles = makeStyles((theme) => ({
  PostBox: {
    backgroundColor: "theme",
    position: "relative",
    padding: "25px",
    // marginBottom: "50px",
    [theme.breakpoints.down("xs")]: {
      padding: "10px",
    },
    "& p": {
      [theme.breakpoints.down("xs")]: {
        fontSize: "12px",
      },
    },
    "& .price": {
      display: "flex",
      alignItems: "center",
      justifyContent: "space-between",
      "& .text": {
        display: "flex",
        alignItems: "center",
        "& h6": {
          fontWeight: "700",
        },
      },
    },
    "& .postImg": {
      width: "100%",
      margin: "16px 0",
      borderRadius: "20px",
      backgroundColor: "#000",
      overflow: "hidden",
      [theme.breakpoints.down("xs")]: {
        margin: "5px 0",
        borderRadius: "10px",
      },
      "& img": {
        width: "100%",
        maxHeight: "380px",
        objectFit: "cover",
      },
    },
    "& label": {
      color: "#BFBFBF",
      [theme.breakpoints.down("xs")]: {
        fontSize: "10px",
      },
    },
    "& .commentBox": {
      borderTop: "0.5px solid #737373",
      borderBottom: "0.5px solid #737373",
      marginTop: "16px",
      padding: "5px 0",
      [theme.breakpoints.down("xs")]: {
        padding: "0px 0",
        marginTop: "10px",
      },
      "& button": {
        [theme.breakpoints.down("xs")]: {
          fontSize: "10px !important",
        },
        "& svg": {
          fontSize: "20px",
          marginRight: "5px",
          [theme.breakpoints.down("xs")]: {
            fontSize: "15px",
          },
        },
      },
    },
    "& .searchaddress": {
      paddingTop: "16px",
      "& .figure": {
        margin: "0",
        marginRight: "15px",
        position: "relative",
        "& .profileimage": {
          height: "50px",
          width: "50px",
          borderRadius: "50%",
          overflow: "hidden",
          backgroundColor: "#101010",
          [theme.breakpoints.down("xs")]: {
            height: "30px",
            width: "30px",
          },
          "& img": {
            width: "100%",
            height: "100%",
            maxWidth: "100%",
            maxHeight: "50px",
            [theme.breakpoints.down("xs")]: {
              maxHeight: "30px",
            },
          },
        },
      },
      "& button": {
        backgroundColor: "#373636",
        height: "48px",
        borderRadius: "5px",
        [theme.breakpoints.down("xs")]: {
          height: "30px",
          "& svg": {
            fontSize: "16px",
          },
        },
      },
    },
    "& .UserBox": {
      display: "flex",
      alignItems: "center",
      marginBottom: "16px",
      [theme.breakpoints.down("xs")]: {
        marginBottom: "10px",
      },
      "& a": {
        textDecoration: "none",
        "& h6": {
          fontWeight: "600",
          color: "#fff",
          [theme.breakpoints.down("xs")]: {
            fontSize: "12px",
          },
        },
      },
      "& small": {
        color: "#BFBFBF",
        [theme.breakpoints.down("xs")]: {
          fontSize: "10px",
        },
      },
      "& figure": {
        margin: "0",
        marginRight: "15px",
        height: "60px",
        width: "60px",
        borderRadius: "50%",
        overflow: "hidden",
        backgroundColor: "#101010",
        display: "flex",
        [theme.breakpoints.down("xs")]: {
          height: "40px",
          width: "40px",
        },
        "& img": {
          width: "100%",
          maxWidth: "100%",
          maxHeight: "60px",
          [theme.breakpoints.down("xs")]: {
            maxHeight: "40px",
          },
        },
      },
    },
  },
  menuShare: {
    position: "absolute",
    right: "16px",
    top: "16px",
    [theme.breakpoints.down("xs")]: {
      right: "0px",
      top: "0px",
    },
  },

  sharemodal: {
    "& button": {
      textAlign: "center",
      "& svg": {
        fontSize: "25px",
      },
    },
  },

  cancelBtn: {
    position: "absolute",
    top: 0,
    right: 0,
  },
  deskiText: {
    "& h4": {
      fontSize: "14px",
      lineHeight: "22px",
    },
  },
  subBox: {
    backgroundColor: "#e31a89",
    borderRadius: " 4px",
    padding: "10px 30px",
    textAlign: "center",
    cursor: " pointer",
    "& h6": {
      fontSize: "15px",
      fontWeight: 600,
    },
  },
}));

export default function (props) {
  const auth = useContext(AuthContext);

  const { data, listPublicExclusiveHandler, isLoadingContent, index } = props;
  console.log("data......", data);
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [isBuyLoading, setIsBuyLoading] = useState(false);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  let isLike = false;
  if (auth.userData?._id && data) {
    const likeUser = data?.likesUsers?.filter(
      (data) => data === auth.userData?._id
    );
    isLike = likeUser?.length > 0;
  }
  let isLikeEmoji = false;
  if (auth.userData?._id && data) {
    const likeUser = data?.reactOnPostPromoted?.filter(
      (data) => data.userId === auth.userData?._id
    );
    // console.log("likeUser", likeUser);
    isLikeEmoji = likeUser?.length > 0;
  }
  const handleClose = () => {
    setAnchorEl(null);
  };
  const [expanded, setExpanded] = React.useState("panel1");
  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };
  const [openShare, setOpenShare] = React.useState(false);
  const handleClickOpenShare = () => {
    setOpenShare(true);
    setAnchorEl(false);
  };
  const handleCloseShare = () => {
    setOpenShare(false);
  };
  const [openReport, setOpenReport] = React.useState(false);
  const [reportMessage, setReportMeesae] = React.useState("");

  const handleClickOpenReport = () => {
    setOpenReport(true);
    setAnchorEl(false);
  };
  const handleCloseReport = () => {
    setOpenReport(false);
  };
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const history = useHistory();
  const [open3, setOpen3] = React.useState(false);
  const [isLoadingReport, setIsLoadingReport] = React.useState(false);
  const [isLoadingReport1, setIsLoadingReport1] = React.useState(false);

  const commentPostHandler = async () => {
    setIsLoadingReport(true);
    setIsLoading(true);
    if (message !== "") {
      try {
        const res = await Axios({
          method: "POST",
          url: Apiconfigs.commentOnPostPromotion,
          headers: {
            token: window.localStorage.getItem("token"),
          },
          data: {
            postPromotionId: data._id,
            message: message,
          },
        });
        if (res.data.responseCode === 200) {
          listPublicExclusiveHandler();
          toast.success(res.data.responseMessage);
          setIsLoading(false);
          setIsLoadingReport(false);

          setMessage("");
        }
      } catch (error) {
        toast.error(error);
        setIsLoading(false);
        setIsLoadingReport(false);
      }
    }
  };
  const likesHandler = async () => {
    try {
      const res = await Axios({
        method: "GET",
        url: Apiconfigs.reactOnPostPromoted + data?._id,
        headers: {
          token: window.localStorage.getItem("token"),
        },
        params: {
          emoji: inputStr,
        },
      });
      if (res.data.responseCode === 200) {
        listPublicExclusiveHandler();
        toast.success(res.data.responseMessage);
      }
    } catch (error) {
      toast.error(error?.response?.data?.responseMessage);
    }
  };
  const hidePostHandler = async () => {
    try {
      const res = await Axios({
        method: "PUT",
        url: Apiconfigs.hideUnHidePost,
        headers: {
          token: window.localStorage.getItem("token"),
        },
        data: {
          postId: data?._id,
        },
      });
      if (res.data.responseCode === 200) {
        listPublicExclusiveHandler();
        setAnchorEl(false);

        toast.success(res.data.responseMessage);
      }
    } catch (error) {
      setAnchorEl(false);
    }
  };
  const [reportMessage1, setReportMeesae1] = React.useState("");

  const reportPostHandler = async () => {
    setIsLoading(true);
    setReportMeesae1(true);
    if (reportMessage !== "") {
      try {
        const res = await Axios({
          method: "POST",
          url: Apiconfigs.createReport,
          headers: {
            token: window.localStorage.getItem("token"),
          },
          data: {
            postId: data._id,
            message: reportMessage,
          },
        });
        if (res.data.responseCode === 200) {
          setReportMeesae1(false);
          listPublicExclusiveHandler();
          setAnchorEl(false);
          toast.success(res.data.responseMessage);
          setIsLoading(false);
          setReportMeesae("");
          setOpenReport(false);
        }
      } catch (error) {
        setReportMeesae1(false);

        toast.error(error);
        setIsLoading(false);
      }
    }
  };
  const isVideo = data?.mediaUrl?.includes(".mp4");

  const buyPostHandler = async (postId) => {
    try {
      setIsBuyLoading(true);

      const res = await Axios.post(
        Apiconfigs.buyPost,
        {
          postId: postId,
          description: "NA",
        },
        {
          headers: {
            token: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.responseCode === 200) {
        listPublicExclusiveHandler();
        toast.success(res.data.responseMessage);
      } else {
        toast.error(res.data.responseMessage);
      }

      setIsBuyLoading(false);
    } catch (error) {
      setIsBuyLoading(false);
      toast.error(error.message);
    }
  };
  const subscribeNowHandler = async (isCheck) => {
    // setIsloading(true);
    await Axios({
      method: "POST",
      url: Apiconfigs.subscribeNow,
      headers: {
        token: window.localStorage.getItem("token"),
      },
      data: {
        collectionId: data.collectionId,
      },
    })
      .then(async (res) => {
        // setIsloading(false);
        if (res.data.responseCode === 200) {
          // auth.updateUserData()
          // history.push('/profile')
          toast.success("You have subscribed successfully");
          if (listPublicExclusiveHandler) {
            listPublicExclusiveHandler();
          }
        } else {
          toast.error("Something went wrong");
        }
      })
      .catch((err) => {
        // setIsloading(false);
        toast.error(err.response.data.responseMessage);

        if (listPublicExclusiveHandler) {
          listPublicExclusiveHandler();
        }

        // toast.error("Something went wrong");
      });
    // } else {
    //   toast.error("Balance is low");
    //   setIsloading(false);
    // }
  };

  const handleCollection = () => {
    colletionDetails();
    setOpen3(true);
  };
  const [viewCollectionDetails, setVieCollectionDetails] = useState();

  const fileExtention = viewCollectionDetails?.image.split(".").pop();

  const fileType =
    fileExtention == "mp4" || fileExtention == "webp"
      ? "video"
      : fileExtention == "mp3"
      ? "audio"
      : "image";

  const colletionDetails = async () => {
    setIsLoading(true);
    try {
      const res = await Axios({
        method: "GET",
        url: Apiconfigs.viewCollection + data?.collectionId,
        headers: {
          token: window.localStorage.getItem("token"),
        },
      });
      if (res.data.responseCode === 200) {
        setVieCollectionDetails(res.data.result);
        setIsLoading(false);
      } else {
        // toast.error(res.data.responseMessage);
      }
      setIsLoading(false);
    } catch (error) {
      setIsLoading(false);
      // toast.error(error.response.data.responseMessage);
    }
  };
  const [inputStr, setInputStr] = useState("");
  const [showPicker, setShowPicker] = useState(false);

  const onEmojiClick = (event, emojiObject) => {
    setInputStr(emojiObject.emoji);
    setShowPicker(false);
  };
  useEffect(() => {
    if (inputStr) {
      likesHandler();
    }
  }, [inputStr]);

  console.log("data?.postId?.mediaUrl---", data?.postId?.mediaUrl);
  return (
    <Paper>
      {data && (
        <>
          {data?.postType === "PRIVATE" && !data?.isSubscribed ? (
            <Box style={{ position: "relative" }}>
              <Box
                className={classes.PostBox}
                style={
                  data?.isSubscribed && data?.postType !== "PRIVATE"
                    ? {}
                    : { filter: "blur(15px)" }
                }
              >
                <IconButton
                  onClick={handleClick}
                  aria-controls="customized-menu"
                  aria-haspopup="true"
                  className={classes.menuShare}
                >
                  <FaEllipsisH />
                </IconButton>

                <Box className="UserBox">
                  <figure>
                    {isLoadingContent ? (
                      <LoadingSkeleton data={8} />
                    ) : (
                      <img
                        src={
                          data?.profilePic
                            ? data?.profilePic
                            : "images/user.png"
                        }
                      />
                    )}
                  </figure>
                  <Box>
                    <Link to="#">
                      {" "}
                      <Typography variant="h6">
                        {data?.userId?.userName
                          ? data?.userId?.userName
                          : data?.userId?.name}
                      </Typography>
                      <Typography variant="h6">
                        {data?.userId?.postTitle}
                      </Typography>
                    </Link>
                    <Typography variant="body2" component="small">
                      {data?.time}
                    </Typography>
                  </Box>
                </Box>
                <Typography variant="body2">{data?.details}</Typography>
                <Box>
                  {/* ?????????//// */}
                  <Box mt={1} mb={1} className="price">
                    {/* <Box className="text">
                      <Typography variant="h6" color="secondary.main">
                        {data?.postType !== "PUBLIC" && <> Price :</>}
                      </Typography>
                      <Typography variant="h6" color="secondary.main">
                        {data?.postTitle}
                      </Typography>
                      &nbsp;&nbsp;
                      <Typography variant="h6" color="secondary.main">
                        {data?.postType !== "PUBLIC" && (
                          <>
                            {data?.amount}
                            {tokenName}
                          </>
                        )}
                      </Typography>
                    </Box> */}
                    {data?.postType !== "PUBLIC" &&
                      data?.userId?._id !== auth?.userData?._id && (
                        <Button
                          variant="contained"
                          color="secondary"
                          // onClick={() => buyPostHandler(data._id)}
                          disabled={isBuyLoading || !data?.isSubscribed}
                        >
                          Buy {isBuyLoading && <ButtonCircularProgress />}
                        </Button>
                      )}
                  </Box>
                  <Box>
                    <a href="https://www.w3schools.com" target="_blank">
                      <figure className="postImg">
                        {isVideo ? (
                          <video
                            width="100%"
                            height="450"
                            // style={{ cursor: "pointer" }}
                            controls
                            // onClick={handleClickOpen2}
                          >
                            <source src={data.mediaUrl} type="video/mp4" />
                          </video>
                        ) : (
                          <img
                            src={data?.mediaUrl}
                            style={{ cursor: "pointer" }}
                          />
                        )}
                        {/* {isLoadingContent ? (
              <LoadingSkeleton data={8} />
            ) : (
              <img src={data?.mediaUrl} />
            )} */}
                      </figure>
                    </a>
                  </Box>
                  <Box>
                    <Grid container alignItems="center">
                      <Grid item xs={5}>
                        {" "}
                        {data?.reactOnPostPromotedCount > 0 ? (
                          <Box>
                            +{data?.reactOnPostPromotedCount}&nbsp; Likes
                          </Box>
                        ) : (
                          <Box>{data?.reactOnPostPromotedCount}&nbsp;Likes</Box>
                        )}
                        {/* <MultyUser data={data?.likesCount} /> */}
                      </Grid>
                      <Grid item xs={7} align="right">
                        <label>{data?.totalComment} Comments</label> &nbsp;
                        &nbsp; &nbsp; &nbsp;
                        <label>0 Share</label>
                      </Grid>
                    </Grid>
                  </Box>
                  <Accordion
                    square
                    // expanded={expanded === "panel1"}
                  >
                    <Box className="commentBox">
                      {showPicker && (
                        <Box className={classes.emojiBox}>
                          <Picker onEmojiClick={onEmojiClick} />
                        </Box>
                      )}
                      <Grid container>
                        <Grid item xs={4}>
                          <Box>
                            {isLikeEmoji ? (
                              <>
                                {" "}
                                {data?.reactOnPostPromoted
                                  ?.filter(
                                    (data) =>
                                      data.userId === auth?.userData?._id
                                    // console.log(
                                    //   "dat>>>>>a",
                                    //   data.userId === auth?.userData?._id
                                    // )
                                  )
                                  .map((data, i) => {
                                    return (
                                      <>
                                        <Button
                                          key={i}
                                          style={{
                                            fontSize: "20px",
                                            cursor: "pointer",
                                          }}
                                          onClick={() =>
                                            setShowPicker((val) => !val)
                                          }
                                          className={classes.socialbox}
                                        >
                                          {data?.emoji}
                                        </Button>
                                      </>
                                    );
                                  })}
                              </>
                            ) : (
                              <>
                                <Button
                                  style={{
                                    fontSize: "20px",
                                    cursor: "pointer",
                                  }}
                                >
                                  <BsEmojiLaughing
                                    className={classes.socialbox}
                                    position="end"
                                    onClick={() => setShowPicker((val) => !val)}
                                  />
                                </Button>
                              </>
                            )}

                            {/* {inputStr && inputStr ? (
                            <Button
                              style={{
                                fontSize: "20px",
                                cursor: "pointer",
                              }}
                              onClick={() => setShowPicker((val) => !val)}
                              className={classes.socialbox}
                            >
                              {inputStr}
                            </Button>
                          ) : (
                            <Button
                              style={{
                                fontSize: "20px",
                                cursor: "pointer",
                              }}
                            >
                              <BsEmojiLaughing
                                className={classes.socialbox}
                                position="end"
                                onClick={() => setShowPicker((val) => !val)}
                              />
                            </Button>
                          )} */}
                          </Box>
                          {/* <Button
                          color="primary"
                          size="large"
                          onClick={likesHandler}
                        >
                          <FaHeart
                            className={classes.socialbox}
                            style={isLike ? { color: "red" } : {}}
                          />{" "}
                          Like
                        </Button> */}
                        </Grid>
                        <Grid item xs={4} align="center">
                          <AccordionSummary
                            aria-controls="panel1d-content"
                            id="panel1d-header"
                          >
                            <Button color="primary" size="large">
                              {" "}
                              <BiCommentDots />
                              Comments
                            </Button>
                          </AccordionSummary>
                        </Grid>
                        <Grid item xs={4} align="right">
                          <Button color="primary" size="large">
                            {" "}
                            <RiShareForwardLine />
                            Share
                          </Button>
                        </Grid>
                      </Grid>
                    </Box>
                  </Accordion>
                  <Box className="searchaddress">
                    <Grid container spacing={1} alignItems="center">
                      <Grid item xs={2} sm={1}>
                        <Box className="figure">
                          <Box className="profileimage">
                            <img
                              src={
                                auth?.userData?.profilePic
                                  ? auth?.userData?.profilePic
                                  : "images/user.png"
                              }
                              alt="user data"
                            />
                          </Box>
                        </Box>
                      </Grid>
                      <Grid item xs={8} sm={10}>
                        <TextField
                          id="outlined-basic"
                          variant="outlined"
                          name="Text Field"
                          placeholder="Write here??..."
                          type="text"
                          value={message}
                          // onChange={(e) => setMessage(e.target.value)}
                          fullWidth
                        />
                      </Grid>
                      <Grid item xs={2} sm={1} align="center">
                        <Button
                          size="large"
                          color="primary"
                          fullWidth
                          disabled={isLoading}
                        >
                          <SendIcon
                            style={{ color: "#E31A89" }}
                            disabled={isLoading}
                          />
                          {/* {isLoading && <ButtonCircularProgress />} */}
                        </Button>
                      </Grid>
                    </Grid>
                  </Box>
                </Box>
              </Box>
              <Box
                style={{
                  position: "absolute",
                  top: "50%",
                  left: "50%",
                  transform: "translate(-50%, -50%)",
                  width: "250px",
                }}
              >
                <Box onClick={handleCollection} className={classes.subBox}>
                  <Typography variant="h6"> Subscribe Collection</Typography>
                  <Typography variant="body2">
                    Price : {data?.amount} &nbsp;{tokenName}
                  </Typography>
                </Box>
                {/* <Button
                variant="contained"
                onClick={handleCollection}
                color="secondary"
              >
                Subscribe Collection
                <br /> &nbsp;
                <Typography>{data?.amount}</Typography>
              </Button> */}
              </Box>
              {open3 && (
                <Dialog
                  fullWidth="sm"
                  maxWidth="sm"
                  open={open3}
                  onClose={() => setOpen3(false)}
                  aria-labelledby="max-width-dialog-title"
                  disableBackdropClick={isLoading}
                  disableEscapeKeyDown={isLoading}
                >
                  <DialogContent>
                    {fileType == "image" && (
                      <Box
                        // id={`imagecard${index}`}
                        className={classes.PhotoBox}
                        // style={{
                        //   background: "url(" + viewCollectionDetails?.image + ")",
                        // }}
                        // onClick={() => {
                        //   history.push("/about-auction");
                        // }}
                      >
                        <img
                          src={viewCollectionDetails?.image}
                          alt=""
                          style={{ height: "368px", width: "553px" }}
                        />
                      </Box>
                    )}
                    {(fileType == "video" || fileType == "audio") && (
                      <Box
                        id={`imagecard${index}`}
                        className={classes.PhotoBox}
                        // onClick={() => {
                        //   history.push("/about-auction");
                        // }}
                      >
                        <video
                          width="100%"
                          loop={false}
                          autoPlay={false}
                          muted={true}
                          controls
                        >
                          <source
                            src={viewCollectionDetails?.image}
                            type="video/mp4"
                          />
                        </video>
                      </Box>
                    )}
                    {/* <Box className={classes.PhotoBox}> */}
                    {/* {isVideo ? (
                  <div>
                    <video width="100%" controls>
                      <source src={data?.nftId[0]?.mediaUrl} type="video/mp4" />
                    </video>
                  </div>
                ) : ( */}

                    {/* <img
                      src={viewCollectionDetails?.image}
                      alt=""
                      style={{ height: "368px", width: "553px" }}
                    /> */}
                    {/* )} */}
                    {/* <img src={data.mediaUrl} alt="" /> */}
                    {/* </Box> */}
                    <Box
                      mt={2}
                      className={classes.bundleText}
                      textAlign="center"
                    >
                      <Typography variant="h4" className="red">
                        {viewCollectionDetails?.name}
                      </Typography>
                    </Box>

                    <Box mt={1} className={classes.deskiText} align="left">
                      <Typography
                        variant="h4"
                        align="left"
                        color="textSecondary"
                      >
                        Collection amount:{" "}
                        <span>
                          {viewCollectionDetails?.amount}&nbsp;{tokenName}
                        </span>
                      </Typography>
                      {/* <Typography
                        variant="h4"
                        align="left"
                        color="textSecondary"
                      >
                        Duration:{" "}
                        <span>
                          {" "}
                          {viewCollectionDetails?.duration} &nbsp; Days
                        </span>
                      </Typography> */}

                      <Typography
                        variant="h4"
                        align="left"
                        color="textSecondary"
                      >
                        Details:{" "}
                        <span> {viewCollectionDetails?.description}</span>
                      </Typography>
                    </Box>
                    {auth.userData &&
                      auth.userLoggedIn &&
                      auth?.userData?._id !== data?.userId && (
                        <Box mt={3} mb={3} textAlign="center">
                          <Button
                            variant="contained"
                            color="primary"
                            size="large"
                            onClick={() => {
                              setOpen3(false);
                            }}
                            disabled={isLoading}
                          >
                            Cancel
                          </Button>
                          &nbsp;&nbsp;&nbsp;
                          {data?.userId?._id !== auth?.userData?._id && (
                            // auth.userData._id !== userId &&
                            <Button
                              variant="contained"
                              color="secondary"
                              size="large"
                              onClick={subscribeNowHandler}
                              // onClick={() => {
                              //   if (auth?.userData.userType === "User") {
                              //     subscribeNowBlockchainHandler(data);
                              //   } else {
                              //     subscribeNowHandler(true);
                              //   }
                              // }}
                              // disabled={isLoading}
                            >
                              Subscribe Now
                              {/* {isLoading ? "pending..." : "Subscribe now"}{" "}
                      {isLoading && <ButtonCircularProgress />} */}
                            </Button>
                          )}
                        </Box>
                      )}
                  </DialogContent>
                </Dialog>
              )}
            </Box>
          ) : (
            <Box className={classes.PostBox}>
              <IconButton
                onClick={handleClick}
                aria-controls="customized-menu"
                aria-haspopup="true"
                className={classes.menuShare}
              >
                <FaEllipsisH />
              </IconButton>
              <StyledMenu
                id="customized-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <StyledMenuItem onClick={handleClickOpenShare}>
                  <ListItemText primary="Share" />
                </StyledMenuItem>
              </StyledMenu>
              <Box className="UserBox">
                <figure>
                  {isLoadingContent ? (
                    <LoadingSkeleton data={8} />
                  ) : (
                    <img
                      src={
                        data?.userId?.profilePic
                          ? data?.userId?.profilePic
                          : "images/user.png"
                      }
                    />
                  )}
                </figure>
                <Box>
                  <Link to="#">
                    {" "}
                    <Typography
                      variant="h6"
                      style={{
                        fontSize: "16px",
                        color: "#fff",
                        fontWeight: "500",
                      }}
                    >
                      {data?.userId?.userName
                        ? data?.userId?.userName
                        : data?.userId?.name}
                    </Typography>
                    <Typography variant="h6">
                      {moment(data?.createdAt).local().fromNow()}
                    </Typography>
                  </Link>
                  <Typography variant="body2" component="small">
                    {data?.time}
                  </Typography>
                </Box>
              </Box>
              <Typography variant="body2">{data?.postTitle}</Typography>
              {data?.details && (
                <div dangerouslySetInnerHTML={{ __html: data?.details }}></div>
              )}
              {/* <Typography variant="h6">{data?.details}</Typography> */}
              <Box>
                {/* ?????????//// */}
                <Box mt={1} mb={1} className="price">
                  {/* <Box className="text">
                    <Typography variant="h6" color="secondary.main">
                      {data?.postType !== "PUBLIC" && <> Price :</>}
                    </Typography>
                    &nbsp;&nbsp;
                    <Typography variant="h6" color="secondary.main">
                      {data?.postType !== "PUBLIC" && (
                        <>
                          {data?.amount}
                          {tokenName}
                        </>
                      )}
                    </Typography>
                  </Box> */}
                  {/* {data?.postType !== "PUBLIC" &&
                    data?.userId?._id !== auth?.userData?._id && (
                      <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => buyPostHandler(data._id)}
                        disabled={isBuyLoading}
                      >
                        Buy {isBuyLoading && <ButtonCircularProgress />}
                      </Button>
                    )} */}
                </Box>
                <Box
                // onClick={() =>
                //   history.push({
                //     pathname: "/comment",
                //     search: data?._id,
                //   })
                // }
                >
                  {data?.postId ? (
                    <a href={data?.url} target="_blank">
                      <figure className="postImg">
                        {isVideo ? (
                          <video
                            width="100%"
                            height="450"
                            // style={{ cursor: "pointer" }}
                            controls
                            // onClick={handleClickOpen2}
                          >
                            <source
                              src={data?.postId?.mediaUrl}
                              type="video/mp4"
                            />
                          </video>
                        ) : (
                          <img
                            src={data?.postId?.mediaUrl}
                            style={{ cursor: "pointer" }}
                          />
                        )}
                        {/* {isLoadingContent ? (
       <LoadingSkeleton data={8} />
     ) : (
       <img src={data?.mediaUrl} />
     )} */}
                      </figure>
                    </a>
                  ) : (
                    <a href={data?.url} target="_blank">
                      <figure className="postImg">
                        {isVideo ? (
                          <video
                            width="100%"
                            height="450"
                            // style={{ cursor: "pointer" }}
                            controls
                            // onClick={handleClickOpen2}
                          >
                            <source src={data?.mediaUrl} type="video/mp4" />
                          </video>
                        ) : (
                          <img
                            src={data?.mediaUrl}
                            style={{ cursor: "pointer" }}
                          />
                        )}
                        {/* {isLoadingContent ? (
         <LoadingSkeleton data={8} />
       ) : (
         <img src={data?.mediaUrl} />
       )} */}
                      </figure>
                    </a>
                  )}
                </Box>
                <Box>
                  <BsFillArrowUpRightSquareFill /> Promoted
                </Box>
                <Box>
                  <Grid container alignItems="center">
                    <Grid item xs={5}>
                      {" "}
                      {data?.reactOnPostPromotedCount > 0 ? (
                        <Box>+{data?.reactOnPostPromotedCount}&nbsp; Likes</Box>
                      ) : (
                        <Box>{data?.reactOnPostPromotedCount}&nbsp;Likes</Box>
                      )}
                      {/* <MultyUser data={data?.likesCount} /> */}
                    </Grid>
                    <Grid item xs={7} align="right">
                      <label>{data?.totalComment} Comments</label> &nbsp; &nbsp;
                      &nbsp; &nbsp;
                      <label>0 Share</label>
                    </Grid>
                  </Grid>
                </Box>
                <Accordion
                  square
                  // expanded={expanded === "panel1"}
                  onChange={handleChange("panel1")}
                >
                  <Box className="commentBox">
                    {showPicker && (
                      <Box className={classes.emojiBox}>
                        <Picker onEmojiClick={onEmojiClick} />
                      </Box>
                    )}
                    <Grid container>
                      <Grid item xs={4}>
                        <Box>
                          {isLikeEmoji ? (
                            <>
                              {" "}
                              {data?.reactOnPostPromoted
                                ?.filter(
                                  (data) => data.userId === auth?.userData?._id
                                  // console.log(
                                  //   "dat>>>>>a",
                                  //   data.userId === auth?.userData?._id
                                  // )
                                )
                                .map((data, i) => {
                                  return (
                                    <>
                                      <Button
                                        key={i}
                                        style={{
                                          fontSize: "20px",
                                          cursor: "pointer",
                                        }}
                                        onClick={() =>
                                          setShowPicker((val) => !val)
                                        }
                                        className={classes.socialbox}
                                      >
                                        {data?.emoji}
                                      </Button>
                                    </>
                                  );
                                })}
                            </>
                          ) : (
                            <>
                              <Button
                                style={{
                                  fontSize: "20px",
                                  cursor: "pointer",
                                }}
                              >
                                <BsEmojiLaughing
                                  className={classes.socialbox}
                                  position="end"
                                  onClick={() => setShowPicker((val) => !val)}
                                />
                              </Button>
                            </>
                          )}

                          {/* {inputStr && inputStr ? (
                            <Button
                              style={{
                                fontSize: "20px",
                                cursor: "pointer",
                              }}
                              onClick={() => setShowPicker((val) => !val)}
                              className={classes.socialbox}
                            >
                              {inputStr}
                            </Button>
                          ) : (
                            <Button
                              style={{
                                fontSize: "20px",
                                cursor: "pointer",
                              }}
                            >
                              <BsEmojiLaughing
                                className={classes.socialbox}
                                position="end"
                                onClick={() => setShowPicker((val) => !val)}
                              />
                            </Button>
                          )} */}
                        </Box>
                        {/* <Button
                          color="primary"
                          size="large"
                          onClick={likesHandler}
                        >
                          <FaHeart
                            className={classes.socialbox}
                            style={isLike ? { color: "red" } : {}}
                          />{" "}
                          Like
                        </Button> */}
                      </Grid>
                      <Grid item xs={4} align="center">
                        <AccordionSummary
                          aria-controls="panel1d-content"
                          id="panel1d-header"
                        >
                          <Button color="primary" size="large">
                            {" "}
                            <BiCommentDots />
                            Comments
                          </Button>
                        </AccordionSummary>
                      </Grid>
                      <Grid item xs={4} align="right">
                        <Button
                          color="primary"
                          size="large"
                          onClick={handleClickOpenShare}
                        >
                          {" "}
                          <RiShareForwardLine />
                          Share
                        </Button>
                      </Grid>
                    </Grid>
                  </Box>
                  {data &&
                    data?.comment?.map((dataList, i) => {
                      return (
                        <AccordionDetails>
                          <CommentPromotionBox
                            key={i}
                            dataList={data}
                            listPublicExclusiveHandler={
                              listPublicExclusiveHandler
                            }
                            data={dataList}
                          />
                        </AccordionDetails>
                      );
                    })}
                </Accordion>
                <Box className="searchaddress">
                  <Grid container spacing={1} alignItems="center">
                    <Grid item xs={2} sm={1}>
                      <Box className="figure">
                        <Box className="profileimage">
                          <img
                            src={
                              auth?.userData?.profilePic
                                ? auth?.userData?.profilePic
                                : "images/user.png"
                            }
                            alt="user data"
                          />
                        </Box>
                      </Box>
                    </Grid>
                    <Grid item xs={8} sm={10}>
                      <TextField
                        id="outlined-basic"
                        variant="outlined"
                        name="Text Field"
                        placeholder="Write here..."
                        type="text"
                        value={message}
                        error={isLoadingReport && message === ""}
                        onChange={(e) => setMessage(e.target.value)}
                        fullWidth
                      />
                      <FormHelperText error>
                        {isLoadingReport && message === "" && (
                          <Box ml={1} style={{ fontSize: "12px" }}>
                            Message is required
                          </Box>
                        )}
                      </FormHelperText>
                    </Grid>
                    <Grid item xs={2} sm={1} align="center">
                      <Button
                        size="large"
                        color="primary"
                        fullWidth
                        onClick={commentPostHandler}
                        disabled={isLoading}
                      >
                        <SendIcon
                          style={{ color: "#E31A89" }}
                          disabled={isLoading}
                        />
                        {/* {isLoading && <ButtonCircularProgress />} */}
                      </Button>
                    </Grid>
                  </Grid>
                </Box>
              </Box>
            </Box>
          )}

          <Dialog
            open={openShare}
            onClose={handleCloseShare}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            maxWidth="xs"
            fullWidth
          >
            <DialogTitle id="alert-dialog-title">{"Share Post"}</DialogTitle>
            <DialogContent>
              <IconButton
                className={classes.cancelBtn}
                onClick={handleCloseShare}
              >
                <MdCancel />
              </IconButton>
              <SocialShareBox
                url={websiteName + "/about-auction?" + data._id}
              />
            </DialogContent>
          </Dialog>

          <Dialog
            open={openReport}
            onClose={handleCloseReport}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            maxWidth="xs"
            fullWidth
          >
            <DialogTitle id="alert-dialog-title">{"Report Post"}</DialogTitle>
            <DialogContent>
              <IconButton
                className={classes.cancelBtn}
                onClick={handleCloseReport}
              >
                <MdCancel />
              </IconButton>

              <Box mb={3}>
                <Typography variant="body2" style={{ fontSize: "12px" }}>
                  Describe why you think this item should be removed from
                  marketplace
                </Typography>
                <Box mt={2}>
                  <TextField
                    id="outlined-basic"
                    variant="outlined"
                    name="Text Field"
                    placeholder="Tell us some details..."
                    type="text"
                    fullWidth
                    multiline
                    maxRows={5}
                    error={reportMessage1 && reportMessage === ""}
                    // rows={5}
                    value={reportMessage}
                    onChange={(e) => setReportMeesae(e.target.value)}
                  />
                  <FormHelperText error>
                    {reportMessage1 && reportMessage === "" && (
                      <Box ml={1} style={{ fontSize: "12px" }}>
                        Report message is required
                      </Box>
                    )}
                  </FormHelperText>
                </Box>
              </Box>
              <Box mb={2} align="right">
                <Button variant="contained" color="primary">
                  Cancel
                </Button>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={reportPostHandler}
                  style={{ marginLeft: "10px" }}
                >
                  Report
                </Button>
              </Box>
            </DialogContent>
          </Dialog>
        </>
      )}
    </Paper>
  );
}
